import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './EditMe.css'
import Card from './Card'
import { ANIMALS, TERRITORIES, ME_FILENAME } from './constants'

const ipfsAPI = require('ipfs-api');
const ipfs = ipfsAPI({host: 'localhost', port: '5001', protocol: 'http'});

function Utf8ArrayToStr(array) {
    var out, i, len, c;
    var char2, char3;

    out = "";
    len = array.length;
    i = 0;
    while(i < len) {
    c = array[i++];
    switch(c >> 4)
    { 
      case 0: case 1: case 2: case 3: case 4: case 5: case 6: case 7:
        // 0xxxxxxx
        out += String.fromCharCode(c);
        break;
      case 12: case 13:
        // 110x xxxx   10xx xxxx
        char2 = array[i++];
        out += String.fromCharCode(((c & 0x1F) << 6) | (char2 & 0x3F));
        break;
      case 14:
        // 1110 xxxx  10xx xxxx  10xx xxxx
        char2 = array[i++];
        char3 = array[i++];
        out += String.fromCharCode(((c & 0x0F) << 12) |
                       ((char2 & 0x3F) << 6) |
                       ((char3 & 0x3F) << 0));
        break;
    }
    }

    return out;
}


class Upload extends Component {

  constructor(props) {
    super(props)
    this.selectAnimal = this.selectAnimal.bind(this)
    this.selectTerritory = this.selectTerritory.bind(this)

    this.state = {
      selectedAnimal: false,
      selectedTerritory: false,
      strHash: null,
      strContent: null,
      imgSrc: null
    }
    this.loadMe = this.loadMe.bind(this)

  }

  saveTextBlobOnIpfs = (blob) => {
    return new Promise(function(resolve, reject) {
      const descBuffer = Buffer.from(blob, 'utf-8');
      ipfs.add(descBuffer).then((response) => {
        console.log(response)
        resolve(response[0].hash);
      }).catch((err) => {
        console.error(err)
        reject(err);
      })
    })
  }

  saveImageOnIpfs = (reader) => {
    return new Promise(function(resolve, reject) {
      const buffer = Buffer.from(reader.result);
      ipfs.add(buffer).then((response) => {
        console.log(response)
        resolve(response[0].hash);
      }).catch((err) => {
        console.error(err)
        reject(err);
      })
    })
  }

  componentWillMount() {
    this.loadMe()
  }

  loadMe() {
    if(this.props.me) {
        this.setState({selectedAnimal: true, selectedTerritory: true})
      } else {
        this.setState({selectedAnimal: false, selectedTerritory: false})
      }
  }
  

  selectAnimal(e, animal) {
    e.preventDefault()
    this.setState({selectedAnimal: true})
    this.props.saveMe(Object.assign({}, this.props.me, { animal }))
  }

  selectTerritory(e, territory) {
    e.preventDefault()
    this.setState({selectedTerritory: true})
    this.props.saveMe(Object.assign({}, this.props.me, { territory }))
  }


  render() {
    const me = this.props.me
    let myAnimal = null
    let myTerritory = null
    if (me) {
      myAnimal = this.props.me.animal
      myTerritory = this.props.me.territory
    }
    const selectedAnimal = this.state.selectedAnimal 
    const selectedTerritory = this.state.selectedTerritory
    const completed = selectedAnimal && selectedTerritory
    const username = this.props.username



    return (
      <div className="Upload container">
      <h2>Your Certificates</h2>
      <p>You have these certificates!You can add your certificate after verification.</p>


		<div>
		      <input type="file" ref="file" id="file" name="file" multiple="multiple"/>
		      <br/>
		      <input ref="ipfsContent"/>
		      <button onClick={() => {
			  var file = this.refs.file.files[0];
			  var reader = new FileReader();
			  // reader.readAsDataURL(file);
			  reader.readAsArrayBuffer(file)
			  reader.onloadend = (e) => {
			    console.log(reader);
			    // 上传数据到IPFS
			    this.saveImageOnIpfs(reader).then((hash) => {
			      console.log(hash);
			      this.setState({
				imgSrc: hash,
				strHash: hash
			      })
			    });
			  }

			  // let ipfsContent = this.refs.ipfsContent.value;
			  // console.log(ipfsContent);
			  // this.saveTextBlobOnIpfs(ipfsContent).then((hash) => {
			  //   console.log(hash);
			  //   this.setState({strHash: hash});
			  // });
			}}>提交到IPFS</button>
		      <p>http://ipfs.io/ipfs/{this.state.strHash}</p>
		      <button onClick={() => {
			  console.log('从ipfs读取数据。')
			  let hash = this.state.strHash;
			  
			  ipfs.cat(hash).then((stream) => {
			    console.log(stream);
			    let strContent = Utf8ArrayToStr(stream);
			    console.log(strContent);
			    this.setState({strContent: strContent});
			  });
			}}>读取数据</button>
		      <h1>{"https://ipfs.io/ipfs/" + this.state.imgSrc}</h1>
		      <img alt="区块链部落"  src={"http://127.0.0.1:8080/ipfs/" + this.state.imgSrc}/>
		      </div>

			<h3>Select your animal</h3>
			<div className="row card-deck">
			{ ANIMALS.map((animal, index) => {
			  const selected = myAnimal && myAnimal.id === animal.id
			  return (
			    <Card path='/animals/' key={index} item={animal} select={this.selectAnimal} selected={selected} />
				)
			  })
			}
			</div>

			<h3 className="select-territory">Select your territory</h3>
			<div className="card-deck">
			{ TERRITORIES.map((territory, index) => {
			  const selected = myTerritory && myTerritory.id === territory.id
			  return (
			    <Card path='/territories/' key={index} item={territory} select={this.selectTerritory} selected={selected} />
				)
			  })
			}
			</div>

        <div className="container row">
          <div className="col-lg-12 done">
            <p>
              <Link to={completed?`/kingdom/${username}`:`/me`} 
                    className="btn btn-primary">Done</Link>
            </p>
          </div>
        </div>
      </div>
    );
  }
}


export default Upload
